import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";
import 'bootstrap/dist/css/bootstrap.min.css';
// import AlignmentExample from './AlignmentExample';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

class Header extends Component {
  titles = [];

  constructor() {
    super();
    this.state = { checked: false };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    var dataThemeAttribute = "data-theme";
    var body = document.body;
    var newTheme =
      body.getAttribute(dataThemeAttribute) === "dark" ? "light" : "dark";
    body.setAttribute(dataThemeAttribute, newTheme);
  }

  render() {
    if (this.props.sharedData) {
      var name = this.props.sharedData.name;
      this.titles = this.props.sharedData.titles.map(x => [ x.toUpperCase(), 1500 ] ).flat();
    }

    const HeaderTitleTypeAnimation = React.memo(() => {
      return <Typical className="title-styles" steps={this.titles} loop={50} />
    }, (props, prevProp) => true);

    const navItems = [
      { section: "About", id: "about" },
      { section: "Skills", id: "skills" },
      { section: "Education", id: "education" },
      { section: "Experience", id: "experience" },
      { section: "Projects", id: "portfolio" },
      // { section: "Resume", id: "resume"},
    ];

    return (
      <header id="home" style={{ height: window.innerHeight - 150, display: 'block' }}>
        <Navbar className="navigation" fixed="top" style={{ background: '#e9d5a1', fontSize: '15px' }}>
        <Nav className="justify-content-center" activeKey="/about">
          {navItems.map((item, i) => (
            <Nav.Item>
              <Nav.Link href={`#${item.id}`}style={{ color: 'black', padding: '10px' }}> 
                {item.section} 
              </Nav.Link>
            </Nav.Item>
          ))}
        </Nav></Navbar>
        
        <div className="row aligner" style={{ height: '100%' }}>
          <div className="col-md-12">
            <div>
              <span className="iconify header-icon" data-icon="la:laptop-code" data-inline="false" style={{ height: '150px', width: '150px' }}></span>
              <br/>
              <h1 className="mb-0">
                <Typical steps={[name]} wrapper="p" />
              </h1>
              <div className="title-container" style={{ height: '50px' }}>
                <HeaderTitleTypeAnimation />
              </div>
              <Switch
                checked={this.state.checked}
                onChange={this.onThemeSwitchChange}
                offColor="#baaa80"
                onColor="#353535"
                className="react-switch mx-auto"
                width={90}
                height={40}
                uncheckedIcon={
                  <span
                    className="iconify"
                    data-icon="twemoji:owl"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "20px",
                      color: "#353239",
                    }}
                  ></span>
                }
                checkedIcon={
                  <span
                    className="iconify"
                    data-icon="noto-v1:sun-with-face"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "10px",
                      color: "#353239",
                    }}
                  ></span>
                }
                id="icon-switch"
              />
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;